import React from "react";
import { Container } from "reactstrap";
import TextField from "@mui/material/TextField";
import { useState ,useEffect } from "react";
import { First } from "react-bootstrap/esm/PageItem";
import ToggleButton from "@mui/material/ToggleButton";
import ToggleButtonGroup from "@mui/material/ToggleButtonGroup";
import Button from "@mui/material/Button";
import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';

function Home() {

    // All states 
    const [History, setHstory] = useState([]);

  const [Result, setResult] = useState(0);

  const [MethodType, setMethodType] = useState(0);


  const [ValueA, setValueA] = useState();

  const [ValueB, setValueB] = useState();

  const [alignment, setAlignment] = React.useState("");

  const [Title, setTitle] = useState("Welcme here... ");

//   State end

const handleDelete = () => {
    console.info('You clicked the delete icon.');
  };
// Method List


  const [theArray, setTheArray] = useState([]);

  const addEntryClick = () => {
    setTheArray(oldArray => [...oldArray,Result]);
    localStorage.setItem("Results", JSON.stringify(theArray))
};

const getHistory= () => {
    // setTheArray(oldArray => [...oldArray, `Result : ${Result}`]);
     
    var retrievedObject = localStorage.getItem('Results');

    var obj = JSON.parse(retrievedObject);
setHstory(obj)
console.log(History)
};
 
// useEffect(()=>{
//   getResults();
//     },[]);



// Method Buttons
  const handleChange = (event, newAlignment) => {
    setAlignment(newAlignment);

  };
// Method Buttons end


  const SetTit = (t) => {
    setMethodType(t)
    switch (t) {
      case 1:
        setTitle("Addtion (+)");
        break;
      case 2:
        setTitle("Substract (-)");
        break;
      case 3:
        setTitle("Multiply (x)");
        break;
      case 4:
        setTitle("Devide (/)");
        break;
     

      default:
        break;
    }
  };

//   Calculation Method
  const calcu = () => {
    const a = Number(ValueA);
    const b = Number(ValueB);

    switch (MethodType) {
      case 1:  
        setResult(a + b);
        break;
      case 2:
        
        setResult(a - b);
        break;
      case 3:
       
        setResult(a * b);
        break;
      case 4:
       
        setResult(a / b);
        break;

      default:
        break;
    }
  };

  //   Calculation Method End

//   Clear fun
  const ClearAll = () => {
    setResult(0);
    setValueA('');
    setValueB('');
    setAlignment("");
    setTitle("Welcme here... ");
    setTheArray([])
  };

  //   Clear fun end

  return (
 
      <Container>
        <div className="row">
            <div className="col-sm py-5 logo text-blue-500"> <h4> EasyToCompare</h4></div>
            <div className="col flex justify-center md:justify-end py-5">

        <div className="card border-0">
            <ToggleButtonGroup 
            color="primary"
            value={alignment}
            exclusive
            onChange={handleChange}
            aria-label="Platform"
          >
            <ToggleButton 
              onClick={() => {
                SetTit(1);
              }} 
              value="Addtion"
              className="btn-block"
            >
              Addtion
            </ToggleButton>
            <ToggleButton
             className="btn-block"
              onClick={() => {
                SetTit(2)
              }}
              value="substrat"
            >
              substract
            </ToggleButton>
            <ToggleButton
            className="btn-block"
              onClick={() => {
                SetTit(3)
              }}
              value="multiply"
            >
              Multiply
            </ToggleButton>
            <ToggleButton
             className="btn-block"
              onClick={() => {
                SetTit(4)
              }}
              value="Devide"
            >
              Devide
            </ToggleButton>
          
          </ToggleButtonGroup>
          </div>
            </div>
        </div>

        

        <div className="text-center mt-5 ">
          <h2  className="m-5 title-head">{Title}</h2>

          <div className="d-flex justify-center">
            <TextField
              id="outlined-number"
              label="First Value"
              type="number"
              className="m-2"
              InputLabelProps={{
                shrink: true,
              }}
              value={ValueA}
              onChange={(e) => setValueA(e.target.value)}
            />
            <TextField
              id="outlined-number"
              label="Second Value"
              type="number"
              className="m-2"
              value={ValueB}
              InputLabelProps={{
                shrink: true,
              }}
              onChange={(e) => setValueB(e.target.value)}
            />

            <TextField
              value={Result}
              required
              className="m-2"
              id="outlined-required"
              label="Result"
              defaultValue="Hello World"
            />
          </div>
          <Button
            onClick={calcu}
            variant="contained"
            size="large"
            className="mt-4"
          >
            Run
          </Button>
           
          <Button
            onClick={addEntryClick}
            variant="contained"
            size="large"
            className="mt-4 mx-2"
          >
            Save
          </Button>

        
          <Button variant="contained"
           size="large"
           className="mt-4 mx-2"
          onClick={ClearAll} value="Clear">
              Clear{" "}
            </Button>
         
        
        </div>
    


    <Stack className="mt-5 d-flex justify-end" direction="row" spacing={1}>
    {theArray.map(entry =>
    
      <Chip label={entry} variant="outlined" />
     
      )}
    </Stack>
      <p className="text-end b-0 pt-16">developed by <a href="https://github.com/Ayan8090"> Ayan khan</a></p>
      </Container>
  
  );
}

export default Home;
